import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { DefaultComponent } from '../../../default/default.component';
import { RouterModule, Routes } from '@angular/router';
import { LayoutModule } from '../../../../layouts/layout.module';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { NgxPaginationModule } from 'ngx-pagination';
import { ProductModule } from '../product.module';
import { AddProductComponent } from './add-product.component';
import { NgMultiSelectDropDownModule } from 'ng-multiselect-dropdown';
import { TabModule } from 'angular-tabs-component';
import { QuillModule } from 'ngx-quill';
import { SelectModule } from 'ng2-select';
import { DropzoneModule } from 'ngx-dropzone-wrapper';
import { DROPZONE_CONFIG } from 'ngx-dropzone-wrapper';
import { DropzoneConfigInterface } from 'ngx-dropzone-wrapper';

let currentUser = JSON.parse(localStorage.getItem('currentUser'));

const routes: Routes = [
    {
        path: "",
        component: DefaultComponent,
        children: [
            {
                path: "",
                component: AddProductComponent
            }
        ]
    },
];

const DEFAULT_DROPZONE_CONFIG: DropzoneConfigInterface = {
    // Change this to your upload POST address:
    url: 'https://admin-api.moretolike.com/api/test',
    headers: { headers: { 'Authorization': 'Bearer ' + currentUser.token, 'Cache-Control': 'max-age=1864000, public' } },
    maxFilesize: 50,
    acceptedFiles: 'image/*'
};




@NgModule({
    imports: [
        CommonModule, DropzoneModule, RouterModule.forChild(routes),

        LayoutModule, FormsModule, ReactiveFormsModule,
        TabModule, NgxPaginationModule, SelectModule, NgMultiSelectDropDownModule.forRoot(),
        QuillModule,
    ],
    declarations: [AddProductComponent,],
    providers: [{
        provide: DROPZONE_CONFIG,
        useValue: DEFAULT_DROPZONE_CONFIG
    }]
})
export class AddProductModule { }
